import React, { useState } from "react";
import styles from "./Comentarios.module.css";

const Comentarios = () => {
  const [userComment, setUserComment] = useState("");
  const [userComments, setUserComments] = useState([]);

  const handleAddComment = () => {
    if (userComment) {
      setUserComments([...userComments, userComment]);
      setUserComment("");
    }
  };

  const handleDeleteComment = (index) => {
    const updatedComments = [...userComments];
    updatedComments.splice(index, 1);
    setUserComments(updatedComments);
  };

  return (
    <div name="Comentarios" className={styles.benefits}>
      <h2 className={styles.benefitTitle}>Comentarios sobre los cursos</h2>
      <div className={styles.cajaContainer}>
        <div className={styles.caja}>
          <div className={styles.imagen}>
            <img
              src={require("../assets/avatar1.png")}
              alt="Imagen 1"
              className={styles.img}
            />
          </div>
          <p className={styles.comentario}>Aguante React</p>
        </div>
        <div className={styles.caja}>
          <div className={styles.imagen}>
            <img
              src={require("../assets/avatar2.png")}
              alt="Imagen 2"
              className={styles.img}
            />
          </div>
          <p className={styles.comentario}>Aprendi tanto que me llamaron de MercadoLibre</p>
        </div>
        <div className={styles.caja}>
          <div className={styles.imagen}>
            <img
              src={require("../assets/avatar3.png")}
              alt="Imagen 3"
              className={styles.img}
            />
          </div>
          <p className={styles.comentario}>Muy bueno el curso, lo recomiendo</p>
        </div>
      </div>
      <div className={styles.caja_comentarios}>
        <h2>¿Te gustaría dejar un comentario?</h2>
        <textarea
          maxLength={75}
          rows="4" // Número de filas fijo
          placeholder="Escribe tu comentario"
          value={userComment}
          onChange={(e) => setUserComment(e.target.value)}
        />
        <button onClick={handleAddComment}>Agregar comentario</button>
      </div>
      {userComments.map((comment, index) => (
        <div key={index} className={styles.caja_usuario}>
          <p className={styles.comentario}>{comment}<button className={styles.btn_eliminar} onClick={() => handleDeleteComment(index)}>🗑️</button></p>
          
        </div>
      ))}
    </div>
  );
};

export default Comentarios;
