import React from "react";
import styles from "./Noticias.module.css";
import Step from "../Components/Step";

const Noticias = () => {
  const steps = [
    {
      text: " Noticia",
      id: 1,
      url: "https://elcomercio.pe/tecnologia/inteligencia-artificial/asi-se-verian-los-personajes-de-los-simpsons-en-la-vida-real-segun-una-ia-fotos-inteligencia-artificial-midjourney-noticia/",
      linkText: "Click aqui"
    },
    {
      text: " Noticia",
      id: 2,
      url: "https://www.lanacion.com.ar/lifestyle/le-preguntaron-a-la-ia-quienes-son-las-personas-mas-inteligentes-del-mundo-y-eligio-a-las-seis-mas-nid05072023/",
      linkText: "Click aqui"
    },
  ];

  return (
    <div name="Noticias" className={styles.howWeWork}>
      <h2 className={styles.title}>Noticias del Portal de Digitalers</h2>
      {steps.map((x) => (
        <Step text={x.text} step={x.id} url={x.url} linkText={x.linkText} />
      ))}
    </div>
  );
};

export default Noticias;
