import React from "react";
import styles from "./Contacto.module.css";

const Contacto = () => {
  return (
    <div name="Contacto" className={styles.contact}>
      <h2>Contacto</h2>
      <form
        className={styles.form}
        method="POST"
        action="https://getform.io/f/718583e8-f2fc-4e02-a970-9f8a6eaa2036"
      >
        <label for="Name" req>Nombre y Apellido</label>
        <input id="Name" name="Name" className={styles.input} required></input>
        <label for="Email">Email</label>
        <input required
          id="Email"
          name="Email"
          type="Email"
          className={styles.input}
        ></input>
        <label for="Message">Mensaje</label>
        <textarea id="Message" className={styles.textArea} maxLength={210} required></textarea>
        <button type="submit">Enviar</button>
      </form>
    </div>
  );
};

export default Contacto;