import React from "react";
import styles from "./Info.module.css";

const Info = () => {
  return (
    <div name="Info" className={styles.services}>
      <h2>¿Qué es digit@lers?</h2>
      <p>
        Es un programa de formación gratuita en programación y oficios digitales con el objetivo de promover el desarrollo de talento digital junior para la industria TIC e impulsar la inserción laboral de jóvenes de todo el país.

        Queremos facilitar el acceso a quienes, por sus circunstancias económicas, se les dificulte acceder a este tipo de capacitaciones.

        Los cursos cuentan con el aval de la Cámara Argentina de la Industria del Software (CESSI).
      </p>
      <img
        className={styles.webImage}
        src={require("../assets/webImage.jpg")}
      ></img>
    </div>
  );
};

export default Info;